body {
  margin: 0;
  font-family: 'Satoshi', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

title {
  font-family: 'Raleway', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@tailwind base;
@tailwind components;
@tailwind utilities;

.App {
  background-color: #f8f3ed;
  padding: 2rem;
  min-height: 100vh;
}

.App-logo {
  height: 10vmin;
  margin-top: 2rem;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  margin-bottom: 2rem;
  font-size: calc(25px + 2vmin);
  font-weight: 900;
  font-family: 'Raleway';
  color: #f1889f;
}

.App-link {
  color: #61dafb;
}

.btn {
  border: none;
  background: none;
  font-family: 'Satoshi';
  font-weight: bold;
  font-size: medium;
  cursor: pointer;
}

.btn:hover {
  color: #f1889f;
}

.buttons {
  width: 30rem;
  grid-gap: 14.5rem;
  gap: 14.5rem;
  display: flex;
  position: absolute;
  bottom: 60px;
  right: 130px;
}

.solution {
  position: absolute;
  bottom: 290px;
  right: 400px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

